import React, { Component } from "react";
import { sortByOfferList, sortByOfferList1 } from "./AppData";

const displayBlock = { display: 'block' };
const displayNone = { display: 'none' };

class LenderResultHeaderRow extends Component {
    constructor(props) {
        super(props);
        this.handleSortByChange = this.handleSortByChange.bind(this);
        this.onSortByClick = this.onSortByClick.bind(this);
        this.state = { displaySortByItems: false };
    }

    handleSortByChange(event) {
        this.props.onSortByChage(event.target.value);
        // this.props.onSortByChage(event.target.attributes['react-value'].value);
    }

    onSortByClick(event) {
        this.setState((prevState) => ({ displaySortByItems: !prevState.displaySortByItems }));
    }

    showHideList(isDisplay) {
        return isDisplay ? displayBlock : displayNone;
    }

    getSortByLabel(key) {
        const selectedSortBy = this.props.showDefaultoffer > 0 ? sortByOfferList1.filter((sortBy) => {
            return sortBy.key === key;
        }) : sortByOfferList.filter((sortBy) => {
            return sortBy.key === key;
        });
        return selectedSortBy[0].label;
    }

    render() {
        return (
            <div className={` clearfix `}>
                <div className="sortby">
                    <label>Sort by:</label>
                    <div className="filter-mobile" onClick={this.onSortByClick} >
                        {/* <span className="active">{this.getSortByLabel(this.props.sortBy)}</span>
                        <ul style={this.showHideList(this.state.displaySortByItems)}>
                            {sortByOfferList.map(sortBy => {
                                return <li key={sortBy.key}
                                    react-value={sortBy.key}
                                    onClick={this.handleSortByChange}
                                    className={this.props.sortBy === sortBy.key ? "active" : ""} >
                                    {sortBy.label}
                                </li>
                            })}
                        </ul> */}
                        <select tabIndex={this.props.tabIndex} className= {` loan-app ${this.props.currentDevice !== 'Mobile' ? ' form-group ' : '' }  ${this.props.currentBrowser === 'ie' ? '' : ' form-control ' }  `}
                            onFocus={this.props.onSortByClick}
                            onChange={this.handleSortByChange}
                           
                            disabled={this.props.disabled}
                            value={this.props.sortBy}
                            id={`sortby`}
                            name={`sortby`}
                             >{this.props.showDefaultoffer > 0 ? sortByOfferList1.map(element => {
                                return <option key={element.key}
                                    value={element.key}>
                                    {element.label}
                                </option>
                            }) : sortByOfferList.map(element => {
                                return <option key={element.key} 
                                    value={element.key} id={element.key} name={element.key}>
                                    {element.label}
                                </option>
                            })}
    
                    </select>                    
                    </div>                   
                    {/* <SelectField columnStyle="col-sm-5 firstSection"
                        OnDropdownClick={this.onSortByClick.bind(this)}
                        GetActiveLabel={this.getSortByLabel(this.props.sortBy)}
                        SelectedValue = {this.state.selectedLoanPurpose}
                        ShowHideList= {this.showHideLoanPurposeList}
                        DisplayItem={this.state.displayLoanPurposeItems}
                        ListData={loanPurposeList1}
                        HandleSelectedOption = {this.handleSortByChange}
                       
                       
                       
                        tabIndex="2"
                        name="loanpurpose" /> */}
                </div>
                {this.props.IsHeadWay && this.props.ApplicationID && 
                    <div className={` headway-offer-appid `}><h4>Application ID: {this.props.ApplicationID} </h4></div>
                }
                {/* {this.props.currentDevice !== 'Web' ? <a className=" addis disc-tooltip ">ADVERTISER DISCLOSURE<span className="tooltiptext mobile">The offers that appear on this site are from third party advertisers from which {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } receives compensation. This compensation may impact how and where products appear on this site (including, for example, the order in which they appear). PrimeRates strives to provide a wide array of offers, but our offers do not represent all financial services companies or products.</span></a> : '' } */}
            </div>
        );
    }
}

export default LenderResultHeaderRow;



