import React, { Component } from "react"; 

class SelectField extends Component {
    
    render() {
        const errorClass = 'has-error';
        const names = [ 'state' ];
        //const autoHeight = this.props.autoHeight ? ' auto-height ' : '';
        return (  
            <div className={`${this.props.columnStyle}`}>
                { names.indexOf(this.props.name) === -1 && <p className="fieldname">{this.props.formFields.title}</p> }
                <span className={`formerror ${this.props.name === "state" ? 'top-margin-error-message has-error-mac' : ''} `} >
                        <span >{!this.props.showError && this.props.formFields.required}</span>
                    </span>
                <div className={`form-group`} >
                <input type="hidden" name={this.props.name} id={this.props.name} value={this.props.SelectedValue}  />
                {/* Custom dropdown start */}

                {/* <div tabIndex={this.props.tabIndex} className= {`loan-app dropdown form-group  ${this.props.disabled ? 'select-disabled' : ''} ${!this.props.showError ? errorClass : ''} ${names.indexOf(this.props.name) === -1 || !this.props.showError ? '' : 'address-margin' }`} 
                onClick={this.props.OnDropdownClick} disabled={this.props.disabled}>
                    <span className={`active `}>{this.props.GetActiveLabel(this.props.SelectedValue)}</span>
                    <ul style={this.props.ShowHideList(this.props.DisplayItem)} className={autoHeight}>
                        {this.props.ListData.map(element => {
                            return <li key={element.key}
                                react-value={element.key}
                                onClick={this.props.HandleSelectedOption}
                                onTouchEnd={this.props.HandleSelectedOption}                             
                                className={this.props.SelectedValue === element.key ? "active" : ""} >
                                {element.label}
                            </li>
                        })}
                    </ul>
                </div> */}
                {/* Custom dropdown end */}
                {/* HTML dropdown start */}
                <select tabIndex={this.props.tabIndex} className= {`loan-app form-control form-group  ${this.props.disabled ? 'select-disabled' : ''} ${!this.props.showError ? errorClass : ''} ${names.indexOf(this.props.name) === -1 || !this.props.showError ? '' : 'address-margin' }`}
                    onFocus={this.props.OnDropdownClick}
                    onBlur={this.props.HandleSelectedOption.bind(this, false)}
                    onTouchEnd={this.props.HandleSelectedOption.bind(this, true)}
                    disabled={this.props.disabled}
                    value={this.props.SelectedValue}
                    onChange={this.props.HandleSelectedOption.bind(this, true)}
                >
                

                 {this.props.ListData.map(element => {
                            return <option key={element.key}
                                value={element.key}>
                                {element.label}
                            </option>
                        })}

                </select>
                {this.props.name === "state" && <p className="fieldname">Don't see your state listed? 
                    <a className="loan-app info-tooltip" disabled={this.props.disabled}>   
                        <span className="tooltiptext tooltip-top-margin-state">If your state is missing, it means we haven't opened for business there just yet. Please check back soon.</span>
                            <i className="fa fa-info-circle" ></i></a> </p>}
                {/* HTML dropdown start */}
                    {/* <span className="formerror " >
                        <span >{!this.props.showError && this.props.formFields.required}</span>
                    </span> */}
                </div>
            </div>
      
        );
    }
}

export default SelectField;


