            creditScore: defaultValues.creditScore,
            defaultSelect: defaultValues.defaultSelect,
            selectedState: defaultValues.defaultSelect,
            selectedHousing: defaultValues.defaultSelect,
            selectedEmployment: defaultValues.defaultSelect,
            selectedCitizenship: defaultValues.defaultSelect,
            selectedHighestEducation: defaultValues.defaultSelect,
            selectedEstimatedCreditScore: defaultValues.defaultSelect,
            selectedTimeAtCurrentAddress: defaultValues.defaultSelect,
            selectedAnyMilitaryService: defaultValues.defaultSelect,
            selectedLoanPurpose: filteredLoanPurpose.length > 0 ? filteredLoanPurpose[0].key :  this.props.PostData && this.props.PostData.loanPurpose ? this.props.PostData.loanPurpose : defaultValues.defaultSelect,
            isFirstNameValid: true,
            isLastNameValid: true,
            isEmailValid: true,
            isStreedAddressValid: true,
            isApptValid: true,
            isCityValid: true,
            isStateValid: true,
            isZipcodeValid: true,
            isBirthdateValid: true,
            isHousingValid: true,
            isEmploymentValid: true,
            isCitizenshipStatusValid: true,
            isHighestEducationValid: true,
            isAnnualIncomeValid: true,
            isEstimatedCreditScoreValid: true,
            isTimeAtCurrentAddressValid: true,
            isAnyMilitaryServiceValid: true,
            isPhoneNumberValid: true,
            isProvideSSNValid: true,
            isLoanAmountValid: true,
            isLoanPurposeValid: true,
            showFirstNameError: true,
            showLastNameError: true,
            showEmailError: true,
            showStreedAddressError: true,
            showApptError: true,
            showCityError: true,
            showZipcodeError: true,
            showBirthdateError: true,
            showAnnualIncomeError: true,
            showAnnualIncomeMinError: true,
            showPhoneNumberError: true,
            showPhoneNumberCodeError: true,
            showProvideSSNError: true,
            showLoanAmountError: true,
            showLoanAmountMinError: true,
            showLoanPurposeError: true,
            FirstNameValue: this.props.PostData && this.props.PostData.firstname ? this.props.PostData.firstname : '',
            //typeof this.props.PostData.firstname !== undefined ? this.props.PostData.firstname : '',
            LastNameValue:  this.props.PostData && this.props.PostData.lastname ? this.props.PostData.lastname : '', 
            //typeof this.props.PostData.lastname !== undefined  ? this.props.PostData.lastname : '',
            EmailValue: this.props.PostData && this.props.PostData.email ? this.props.PostData.email : '', 
            //typeof this.props.PostData.email !== undefined  ? this.props.PostData.email : '',
            EmailChangedValue: '',
            StreedAddressValue: '',
            ApptValue: '',
            CityValue: '',
            ZipcodeValue: '',
            BirthdateValue: '',
            DisplayAnnualIncomeValue:'',
            AnnualIncomeValue: '',
            PlainAnnualIncome: '',
            PhoneNumberValue: this.props.PostData && this.props.PostData.phonenumber ? GetPhoneMask(this.props.PostData.phonenumber) : '',
