import React, { Component } from "react";

class LenderResultRowDetail extends Component {

  constructor(props) {
    super(props);
    this.counter = 0;
    this.handleOfferDetailToggle = this.handleOfferDetailToggle.bind(this);
  }

  handleOfferDetailToggle(event) {
    // event.preventDefault();
    this.props.onHideDetailClick(event);
  }

  generateHideDetailLink() {
    const lenderId = this.props.lenderId;
    this.counter = this.counter + 1;
    //const atag = `	&nbsp;<a key='detail-div-a-'${this.counter}-${lenderId} href=#${lenderId} onClick=${this.handleOfferDetailToggle}>${this.props.toggleOfferDetailLabel}</a>`;
    return <a key={'detail-div-a-' + this.counter + '-' + lenderId} href={`#${this.props.hideLink}`} onClick={this.handleOfferDetailToggle}>{this.props.toggleOfferDetailLabel}</a>;
  }

  render() {
    const lenderId = this.props.lenderId;
    if (!this.props.isDetailExpanded) {
      return null;
    }

    // const hideDetailLink = <a key={'detail-div-a-' + this.randomNumberGenerator(1,20) + '-' + lenderId} href={`#${lenderId}`} onClick={this.handleOfferDetailToggle}>{this.props.toggleOfferDetailLabel}</a>;
    let disclaimerHideLink = '';
    if (this.props.disclaimer !== null) {
      disclaimerHideLink = this.generateHideDetailLink();
    }
    //const offerDisc = `<p>In addition to the interest charged on your loan, some lenders may also add origination or other fees. The interest and fees cost displayed is an estimate based on available information, and may not reflect the actual full cost of your loan. Please review in full the disclosures provided by your lender of choice before accepting your loan.</p>`;
    return (
      <div key={'detail-div-' + lenderId} className={`loan-det ${this.props.rowClass}`} >
        {this.props.disclaimer !== null ? <p key={'detail-div-p-' + lenderId} dangerouslySetInnerHTML={{ __html: `${this.props.disclaimer} ` }}></p> : ''}
        {disclaimerHideLink}
      </div>
    );
  }
}

export default LenderResultRowDetail;



