import React, { Component } from "react";
class CheckBoxField extends Component {
    render() {
        const {name} = this.props;
        const {Title} = this.props;
        return (
            <div className={`${this.props.columnStyle}`}>
           

                {/* <input className={` ${this.props.currentDevice !== 'Web' ? 'checkbox chckbox-width' : ' chekbox-alignment'}`} name={name} id={`chk-${name}`} type="checkbox" key={`input-${this.props.key}`} /> */}

                <input className={` ${this.props.currentDevice !== 'Web' ? 'checkbox chckbox-width' : 'chckbox-width chekbox-alignment'}`} name={name} id={`chk-${name}`} type="checkbox" key={`input-${this.props.keyID}`}
                onChange={this.props.handleOnChange.bind(this)} />
                <p className={` ${this.props.currentDevice !== 'Web' ? ' checkbox-title ' : ' checkbox-title '}`} name={name} id={name}  key={`p-chk-${this.props.keyID}`}> {Title}</p>                  
            </div>
        );
    }
}
export default CheckBoxField;


