import React, { Component } from "react";
import LoanIntroduction from "./LoanIntroduction";
import PrequalifiedOffers from "./PrequalifiedOffers";
// import OtherLoanOffers from "./OtherLoanOffers";
// import TypeOfLoan from "./TypeOfLoan";
// import FilterGroupContent from "./FilterGroupContent";

//Local only
 /*
import Configuration from "./json/PL_LA_Configuration.json";
 */

// Production only
 import { Configuration, getConfigurationJson } from "./Configuration";

let settings = null;
if (process.env.NODE_ENV !== 'production') {
    settings = Configuration;
} else {
    // Production only
     const promise = getConfigurationJson();
     promise.then(result => {
         settings = result.Configuration;
     });
}

function getKeyValueFromObject(lookupObject, keyName) {
    if (keyName in lookupObject)
        return lookupObject[keyName];
    for (var key in lookupObject) {
        if (typeof lookupObject[key] === typeof {}) {
            return getKeyValueFromObject(lookupObject[key], keyName);
        }
    }
    return null;
}

class MainPanelContainer extends Component {
    constructor(props) {
        super(props);
        settings = this.props.configSettings;
        let matchedLenderInfo =null;
        if(this.props.PartnerId ) {
            matchedLenderInfo = this.props.configSettings.lenderInfo.filter(lenderInfo => {
                return this.props.PartnerId === lenderInfo.partnerId && lenderInfo.featured === 1;
            });
            if(matchedLenderInfo !== null && matchedLenderInfo.length > 0) {
                this.setState({ selectedLeder: matchedLenderInfo[0].lender });
            }           
        }
        this.state = {
            selectedLeder: matchedLenderInfo !== null && matchedLenderInfo.length > 0 ? matchedLenderInfo[0].lender: '',
        }

    }
  
    getLoanTermFromOffer(offer) {
        if ('term' in offer) {
            return parseInt(offer.term, 10);
        }
        else if ('term_in_months' in offer) {
            return parseInt(offer.term_in_months, 10);
        }
        else if ('loanTerm' in offer) {
            return parseInt(offer.loanTerm, 10);
        }
        return 0;
    }
    getDefaultDiscalimer(lender) {
        let disclaimer='';
        const matchedLenderInfo = this.props.configSettings.lenderInfo.filter(lenderInfo => {
            return lender === lenderInfo.lender;
        });
        if(matchedLenderInfo !== null && matchedLenderInfo.length > 0) {
            disclaimer = matchedLenderInfo[0].disclaimer;
        }
        return disclaimer;
    }
    getLoanAmountFromOffer(offer) {
        if ('loanAmount' in offer) {
            return parseInt(offer.loanAmount, 10);
        }
        else if ('loan_amount' in offer) {
            return parseInt(offer.loan_amount, 10);
        }

        return 0;
    }

    addLoanTermLoanAmountProperties(offers) {
        offers.forEach(offer => {
            offer.loanTermInMonths = this.getLoanTermFromOffer(offer);
            offer.loanAmount = this.getLoanAmountFromOffer(offer);
        });

        return offers;
    }

    sortRecords(rows) {
        rows.sort((a, b) => {
            let termA = a.Term;
            let termB = b.Term;

            let loanAmountA = a.LoanAmount;
            let loanAmountB = b.LoanAmount;

            if (loanAmountA === loanAmountB) {
                return termA - termB;
            }
            else {
                return loanAmountA - loanAmountB;
            }
        });

        return rows;
    }

    getLendersFromFilteredOffers(offers) {
        const lenders = [];
        offers.forEach(offer => {
            if (lenders.indexOf(offer.lender) === -1) {
                lenders.push(offer.lender);
            }
        });
        return lenders;
    }

    isEmptyObject(obj) {
        for (var key in obj) {
            if (obj.hasOwnProperty(key))
                return false;
        }
        return true;
    }

    getOffersOtherThanSelectedLoanAmpount(loanAmount, offers) {
        const intLoanAmount = parseInt(loanAmount, 10);
        const otherLoanAmountOffers = offers.filter((offer) => {
            return parseInt(offer.LoanAmount, 10) !== intLoanAmount;
        });
        return this.sortRecords(otherLoanAmountOffers);
    }

    getOffersOfSelectedLoanAmpount(loanAmount, offers) {
        const intLoanAmount = parseInt(loanAmount, 10);
        const selectedOffers = offers.filter((offer) => {
            return parseInt(offer.LoanAmount, 10) === intLoanAmount;
        });
        return this.sortRecords(selectedOffers);
    }
    getLendersOfferCount(offers) {
        let offerCount = 0;
        if(this.props.PartnerId) {
            const matchedLenderInfo = this.props.configSettings.lenderInfo.filter(lenderInfo => {
                return this.props.PartnerId === lenderInfo.partnerId;
            });            
            if(matchedLenderInfo !== null && matchedLenderInfo.length > 0) {
                const selectedOffers = offers.filter((offer) => {
                    if(!this.isEmptyObject(offer.Lender) && offer.Lender === matchedLenderInfo[0].lender){
                        offerCount++;
                    }
                    return selectedOffers;
                });                 
            }           
        }
        return offerCount;
    }
    getOtherOfferCount(offers) {
        let offerCount = 0;
        if(this.props.PartnerId) {
            const matchedLenderInfo = this.props.configSettings.lenderInfo.filter(lenderInfo => {
                return this.props.PartnerId === lenderInfo.partnerId;
            });            
            if(matchedLenderInfo !== null && matchedLenderInfo.length > 0) {
                const selectedOffers = offers.filter((offer) => {
                    if(!this.isEmptyObject(offer.Lender) && offer.Lender !== matchedLenderInfo[0].lender){
                        offerCount++;
                    }
                    return selectedOffers;
                });                
            }           
        }
        return offerCount;
    }
    unFormatAmount(formattedValue) {
        if(formattedValue !== undefined) {
        return formattedValue.replace('$', '').replace(/,/g, ''); }
        return formattedValue;
    }

    getLenderMapFromConfig(lender) {
        const matchedLenderMaps = settings.lenderInfo ? settings.lenderInfo.filter(lenderofferMap => {
            return lender.toLowerCase() === lenderofferMap.lender.toLowerCase();
        }) : null;

        return matchedLenderMaps !== null && matchedLenderMaps.length > 0 ? matchedLenderMaps[0] : null;
    }

    getLenderDisclaimers(lenders) {
        const lenderDisclaimers = [];
        for (var lender in lenders) {
            if (lenders.hasOwnProperty(lender)) {
                const currentLenderInMap = this.getLenderMapFromConfig(lender);
                if (currentLenderInMap !== null && currentLenderInMap.disclaimerKey.length > 0) {
                    const disclaimer = getKeyValueFromObject(lenders[lender], currentLenderInMap.disclaimerKey);
                    lenderDisclaimers.push({
                        lender: lender,
                        disclaimer: disclaimer
                    });
                }
            }
        }

        return lenderDisclaimers;
    }
    getBestOffers(offers, loanamount, lenderArray) {
        let allLoanOffers = [];
        const applicationId = window.lenderResponse.Response && window.lenderResponse.Response.ApplicationID ? window.lenderResponse.Response.ApplicationID : '';
        const tempLenderarray = [];
        if(offers !== null) {
            
            if(this.props.IsHeadWay) {
                var filter95percentOffer = offers.filter(offer => { 
                    return parseInt( Math.floor(offer.LoanAmount), 10) >= ( loanamount * 0.90);
                    });
                    if(filter95percentOffer.length > 0) {
                        offers = [];
                        offers = filter95percentOffer;
                    } else {
                        //fetch highest amount records
                        offers.sort((a, b) => {
                            if(a.offer !== null && a.offer !== undefined) {
                                let mpA = Math.floor(a.LoanAmount);
                                let mpB = Math.floor(b.LoanAmount);
                                return  mpB - mpA; 
                            }
                        });
                        if(offers.length > 0) {
                            filter95percentOffer = offers.filter(offer => { 
                                return parseInt( Math.floor(offer.LoanAmount), 10) ===  Math.floor(offers[0].LoanAmount);
                            });
                            if(filter95percentOffer.length > 0) {
                                offers = [];
                                offers = filter95percentOffer;
                            } 
                        }
        
                    }
                }
            for(var i = 0; i < lenderArray.length; i++) {
                var closest = offers.filter(offer => {
                    return lenderArray[i] === offer.Lender;
                });
                if(this.props.IsHeadWay) {
                    closest.sort((a, b) => {                   
                        let mpA = a.MonthlyPayment;
                        let mpB = b.MonthlyPayment;
                        return mpA - mpB; 
                    }); }
                else {
                    closest.sort((a, b) => {
                        let mpA = a.Term;
                        let mpB = b.Term;
                        return mpA - mpB; 
                    });                    
                }
                var bestmatched = closest.filter(offer => {
                    return offer.Term > 24 && offer.displayOffer !== 'false';
                });
                if(bestmatched !== null && bestmatched !== undefined && bestmatched.length > 0) {
                   var filtered= bestmatched.reduce(function(prev, curr) {                
                        return (Math.abs(  Math.floor(curr.LoanAmount) - loanamount) < Math.abs( Math.floor(prev.LoanAmount) - loanamount) ? curr : prev);
                    });
                    tempLenderarray.push(filtered);
                } else {
                    var lessOrEqual2Years = closest.filter(offer => {
                        return offer.Term <= 24;
                    });
                    if(lessOrEqual2Years !== null && lessOrEqual2Years !== undefined && lessOrEqual2Years.length > 0) {
                        filtered= lessOrEqual2Years.reduce(function(prev, curr) {                
                             return (Math.abs(  Math.floor(curr.LoanAmount) - loanamount) < Math.abs( Math.floor(prev.LoanAmount) - loanamount) ? curr : prev);
                         });
                         tempLenderarray.push(filtered);
                     }
                }
            }
           
            tempLenderarray.forEach((offer) => {
                // var isExist = allLoanOffers.find(o => o.Lender === offer.Lender);
                //  if(!isExist) {
                var offerCount = offers.filter(f => (f.Lender === offer.Lender && f.displayOffer !== "false")).length;
                if(offer.displayOffer !== "false" && offer.Lender !== '') {
                    allLoanOffers.push({
                        Lender: offer.Lender,
                        lenderImage:  `https://${window.cdn  !== undefined ? window.cdn : window.location.hostname}${this.getLenderMapFromConfig(offer.Lender).lenderLogo}`,
                        OfferURL: window.disableClaimOffer ? '' : offer.OfferURL,
                        disclaimer: offer.Disclaimer,
                        Term: Math.floor(offer.Term /12),
                        LoanAmount:  Math.floor(offer.LoanAmount),
                        MonthlyPayment: offer.MonthlyPayment,
                        OfferID: offer.OfferID,
                        secure: offer.Secure,
                        isSelectedLender: offer.Lender === this.state.selectedLeder ? true : false,
                        ApplicationId: applicationId,
                        APR: offer.APR,
                        OfferCount: offerCount
                    });  
                } 
             //}
            });
        }  
 
        return allLoanOffers;
    }
    getAllOffersFromResponse(offers, loanAmount) {
        let allLoanOffers = [];
        const applicationId = window.lenderResponse.Response && window.lenderResponse.Response.ApplicationID ? window.lenderResponse.Response.ApplicationID : '';
        if(offers !== null) {
            offers.forEach((offer) => {
                if(offer.displayOffer !== "false" && offer.Lender !== '') {
                    allLoanOffers.push({
                        Lender: offer.Lender,
                        lenderImage: `https://${window.cdn !== undefined ? window.cdn : window.location.hostname}${this.getLenderMapFromConfig(offer.Lender).lenderLogo}`,
                        OfferURL: window.disableClaimOffer ? '' : offer.OfferURL,
                        disclaimer: offer.Disclaimer,
                        Term: Math.floor(offer.Term /12),
                        LoanAmount:   Math.floor(offer.LoanAmount),
                        MonthlyPayment: offer.MonthlyPayment,
                        OfferID: offer.OfferID,
                        secure: offer.Secure,
                        isSelectedLender: offer.Lender === this.state.selectedLeder ? true : false,
                        ApplicationId: applicationId,
                        APR: offer.APR,
                        OfferCount: 0,
                    }); 
                }       
            });
        }  
        if(this.props.IsHeadWay) {
        var filter95percentOffer = allLoanOffers.filter(offer => { 
            return parseInt(  Math.floor(offer.LoanAmount), 10) >= ( loanAmount * 0.90);
            });
            if(filter95percentOffer.length > 0) {
                allLoanOffers = [];
                allLoanOffers = filter95percentOffer;
            } else {
                //fetch highest amount records
                allLoanOffers.sort((a, b) => {
                    return  b.LoanAmount - a.LoanAmount; 
                });
                if(allLoanOffers.length > 0) {
                    filter95percentOffer = allLoanOffers.filter(offer => { 
                        return parseInt( Math.floor(offer.LoanAmount), 10) === allLoanOffers[0].LoanAmount;
                    });
                    if(filter95percentOffer.length > 0) {
                        allLoanOffers = [];
                        allLoanOffers = filter95percentOffer;
                    } 
                }

            }
        }
        return allLoanOffers;

    }

    getLoanTerms(offers) {
        let arrLoanTerm = [];
        const loanTerms = offers.filter(offer => {
            console.log(` offer term in year; ${Math.floor(offer.Term/12)}`);
            console.log(`exist : ${arrLoanTerm.indexOf(Math.floor(offer.Term/12))}`);
            if(arrLoanTerm.indexOf(Math.floor(offer.Term/12)) === -1 ) {
                if(offer.Term !== undefined) {
                     console.log(`added: ${Math.floor(offer.Term/12)}`);
                arrLoanTerm.push(Math.floor(offer.Term/12));
                }
            }
        });
        var sortedLoanTerms = arrLoanTerm.sort((a, b) => {
            return a - b;
        });
        return sortedLoanTerms;
    }
    getLenders(offers) {
        let arrLenders = [];
        const loanTerms = offers.filter(offer => {
            if(arrLenders.indexOf(offer.Lender) === -1 ) {
                if(offer.Lender !== undefined)
                arrLenders.push(offer.Lender);
            }
        });
        return arrLenders.sort();
    }
    getMaxLoanAmountOffer(offers, sortBy) {
        var offer;
        if (sortBy === 0) {
            offer = offers.sort((a, b) => {
                let mpA = a.LoanAmount;
                let mpB = b.LoanAmount;

                return mpA - mpB;
            });
        }
        else if (sortBy === 1) {
            offer = offers.sort((a, b) => {
                let mpA = a.LoanAmount;
                let mpB = b.LoanAmount;

                return mpB - mpA;
            });
        }
        return offer[0];
    }
    getLenderCountForHeadway(bestOffers,offers ) {
        var lender = null; 
        if ((bestOffers !== null && bestOffers.length > 1) && (offers !== null && offers.length > 5))
        {
            lender = this.getLenders(bestOffers);
        } else {
            lender =this.getLenders(offers);
        }
        return lender;
    }
    render() {       
        const hasError = typeof window.hasErrorInWebservice !== typeof undefined
            ? window.hasErrorInWebservice : false;
        if (typeof window.lenderResponse === typeof undefined ||
            window.lenderResponse === null || window.lenderResponse.Response === typeof undefined || window.lenderResponse.Response === null
        ) {
            return (
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="box-panel clearfix" id="mainbox" >
                                <div className="container-box-content" id="mainboxcontent">
                                    We are currently having difficulty communicating with our partners. We apologize for the inconvenience. Our engineers have been notified of the issue and are working to resolve it. Please try again later.
                        </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        let loanAmount = this.unFormatAmount(window.loanAmount);
        const isReusableOffers= window.lenderResponse.Response.TransactionType  === "reusable_offers_check" ? true : false;
        // console.log(this.getAllOffersFromResponse(window.lenderResponseNew.Response.Lenders));
        if(isReusableOffers) { loanAmount = window.lenderResponse.Response.ApplicationInfo.loanamount; }
        // const offers = this.addLoanTermLoanAmountProperties(window.lenderResponse.Response.Offers);
        //const offers = window.lenderResponse.Response ? this.getAllOffersFromResponse(window.lenderResponse.Response.Lenders, loanAmount) : null;
        let lenders = window.lenderResponse.Response.Offers ? this.getLenders(window.lenderResponse.Response.Offers) : null;
        const loanTerms = window.lenderResponse.Response.Offers ? this.getLoanTerms(window.lenderResponse.Response.Offers) : null;
        const offers = window.lenderResponse.Response ? this.getAllOffersFromResponse(window.lenderResponse.Response.Offers, loanAmount) : null;
        const bestOffers = window.lenderResponse.Response ? this.getBestOffers(window.lenderResponse.Response.Offers, loanAmount, lenders) : null;
        const selectedOffers = offers ? this.getOffersOfSelectedLoanAmpount(loanAmount, offers) : null;
        const otherLoanOffers = offers ? this.getOffersOtherThanSelectedLoanAmpount(loanAmount, offers) : null;
        const lenderOffers = offers ? this.getLendersOfferCount(offers) : 0;  
        const otherOffers = offers ? this.getOtherOfferCount(offers) : 0;
        lenders = this.getLenderCountForHeadway(bestOffers, offers);
        const isOfferAvailable = lenderOffers === 0 &&  otherOffers === 0 ? false : true;
        const lenderDisclaimers = hasError ? [] : []; // this.getLenderDisclaimers(window.lenderResponse.Response.Lenders);
         
        const minLoanAmount = offers ? this.getMaxLoanAmountOffer(offers,0) : 0;
        const maxLoanAmount = offers ? this.getMaxLoanAmountOffer(offers,1) : 0;
        
        const uuid = isReusableOffers ? window.lenderResponse.Response.ApplicationInfo.uuid : window.lenderResponse.Response.uuid;
        return (
            <div className="container">
                {((selectedOffers !== null && selectedOffers.length > 0) || (otherLoanOffers !== null && otherLoanOffers.length > 0)) && 
                    <LoanIntroduction isOfferAvailable={isOfferAvailable} lenderOfferCount={lenderOffers} otherOfferCount={otherOffers}
                    offers={offers} lendercount={lenders} loanPurpose={isReusableOffers ? window.lenderResponse.Response.ApplicationInfo.loanpurpose : this.props.loanPurpose} currentDevice={this.props.currentDevice}
                        lenders={this.state.selectedLeder} loanAmount={loanAmount} IsHeadWay={this.props.IsHeadWay} selectedOffers={selectedOffers}
                        IsReusableOffers={isReusableOffers}  ApplicationInfo={isReusableOffers ? window.lenderResponse.Response.ApplicationInfo : null}/>
                }
                <div className="row">
                    <div className={`col-md-12 ${this.props.currentDevice === 'Mobile' && this.props.IsOfferPage ? ' mobile-view ' : '' }`}>
                        <PrequalifiedOffers loanAmount={loanAmount} IsOfferPage={this.props.IsOfferPage}
                            redirectUrl={settings.redirectUrl} loanTerms={loanTerms} minLoanAmount={minLoanAmount} maxLoanAmount={maxLoanAmount}
                            hasError={hasError} disclaimers={lenderDisclaimers} lenders={lenders} configSettings={this.props.configSettings}
                            offers={offers} isOtherLoanAvailable={otherLoanOffers !== null ? otherLoanOffers.length > 0 : null} 
                            UUID={uuid} IsHeadWay={this.props.IsHeadWay} currentDevice={this.props.currentDevice}
                            currentBrowser={this.props.currentBrowser} IsReusableOffers={isReusableOffers}
                            ApplicationInfo={isReusableOffers ? window.lenderResponse.Response.ApplicationInfo : null}
                            bestOffers={bestOffers}/> 
                    </div>
                </div>
                 
                {/* <TypeOfLoan disclaimers={lenderDisclaimers} settings={settings} /> */}
            </div>
        );
    }
}

export default MainPanelContainer;


