    ValidateInfo() {
        if(!this.state.initialDisabled) {
            let isValid = true;          
            
            if(!this.state.BirthdateValue) {  this.setState({isBirthdateValid : false}); isValid = false; } else 
            {
                if(this.state.BirthdateValue.length < 10) {
                    this.setState({isBirthdateValid : false,  showBirthdateError: true });  isValid = false; }
                else { 
                    const age = calculateAge(new Date(this.state.BirthdateValue),new Date());
                    if(age < 18 || isNaN(age)) {
                        this.setState({isBirthdateValid : true,  showBirthdateError: false });
                        isValid = false; 
                    } else {
                        this.setState({isBirthdateValid : true, showBirthdateError: true}); 
                    }
                }             
            }
            if(!this.state.AnnualIncomeValue) {  this.setState({isAnnualIncomeValid : false}); isValid = false; } else { 
                let annualIncome = unFormatAmount(this.state.AnnualIncomeValue);
                    if(annualIncome < constantVariables.annualIncome) {
                        this.setState({isAnnualIncomeValid : true, showAnnualIncomeMinError: false }); isValid = false;
                    } else {
                        this.setState({isAnnualIncomeValid : true, showAnnualIncomeMinError: true, PlainAnnualIncome:annualIncome });
                    }
                }
           if(!this.state.ProvideSSNValue) {  this.setState({isProvideSSNValid : false}); isValid = false; } else {  
                this.setState({isProvideSSNValid : true});
                if(this.state.ProvideSSNValue.length !== 11) {this.setState({showProvideSSNError : false }); isValid = false; }
                 else 
                 {  if(!IsSSNGroupValid(this.state.ProvideSSNValue)) 
                    { 
                        this.setState({ showProvideSSNError: false });  isValid = false;
                    } else {
                        this.setState({ showProvideSSNError: true });
                    } 
                }
            }
            if(this.state.selectedEstimatedCreditScore === 'Select') {  this.setState({isEstimatedCreditScoreValid : false}); isValid = false; }
            if(this.state.selectedEmployment === 'Select') {  this.setState({isEmploymentValid : false}); isValid = false; }
            //if(this.state.selectedCitizenship === 'Select') {  this.setState({isCitizenshipStatusValid : false}); isValid = false; }
            //if(this.state.selectedHighestEducation === 'Select') {  this.setState({isHighestEducationValid : false}); isValid = false; }
            // let isAgreeTerm = document.getElementById('frmPrequalify').agreeterms.checked;
            // let isAgreeConsent = document.getElementById('frmPrequalify').agreeconsent.checked;
            // if(!isAgreeTerm) { this.setState({ AgreeTerms: false }); isValid = false;}
            // if(!isAgreeConsent) { this.setState({ AgreeConsent: false }); isValid = false;}
            // let isAgreeEmail = document.getElementById('frmPrequalify').agreeemail.checked;
            // if(!isAgreeEmail) { this.setState({ AgreeEmail: false }); }
            let isAgreePqTerms = document.getElementById('frmPrequalify').agreePqTerms.checked;
            if(!isAgreePqTerms) { this.setState({ AgreePqTerms: false }); isValid = false;}
            let isAgreeTCPA = document.getElementById('frmPrequalify').agreeTCPA.checked;
            if(!isAgreeTCPA) { this.setState({ AgreeTCPA: false }); } else { this.setState({ AgreeTCPA: true }); }
            if(isValid)
            {            
                this.setState({ isError: false });
            }
            else {
                this.setState({ isError: true});
            }
            return isValid;
        }
    }
     
    handleBirthdateChange(isFocus, event) {
        if(!this.state.initialDisabled) {
            let birthdate = GetDateMask(event.target.value);
            this.setState({ BirthdateValue: birthdate, isBirthdateValid : true, showBirthdateError: true, isError: false,showDOBMessage : '' });
            if(!isFocus) {
                if(!birthdate) {  this.setState({isBirthdateValid : false, showBirthdateError: true}); } 
                else 
                {
                    if(birthdate.length < 10) {
                        this.setState({isBirthdateValid : false,  showBirthdateError: true }); }
                    else {
                        const message = checkValidDate(birthdate);
                        if(message === '') {
                            const age = calculateAge(new Date(birthdate),new Date());
                            if(age > 100) {
                                this.setState({isBirthdateValid : true,  showBirthdateError: false, showDOBMessage: 'Invalid birth date' });
                            } else
                            if(age < 18  || isNaN(age)) {
                                this.setState({isBirthdateValid : true,  showBirthdateError: false, showDOBMessage: '' }); 
                            } else {
                                this.setState({isBirthdateValid : true, showBirthdateError: true}); 
                            }
                        }
                        else {this.setState({isBirthdateValid : true,  showBirthdateError: false, showDOBMessage: `Please provide valid ${message}`} );  }
                    }             
                }
            }
        }
    }
    handleAnnualIncomeChange(isFocus, event) {
        if(!this.state.initialDisabled) {
            let annualIncome = unFormatAmount(event.target.value);
            if(isValidInteger(annualIncome)) {
                this.setState({ AnnualIncomeValue: currencySymbol + AmountFormatting(annualIncome), isAnnualIncomeValid : true, showAnnualIncomeMinError: true, annualIncomeFocus: isFocus, isError: false });
                if(!isFocus) {      
                    if(!annualIncome) {  this.setState({isAnnualIncomeValid : false, showAnnualIncomeMinError: true}); } else { 
                        if(annualIncome < constantVariables.annualIncome) {
                            this.setState({isAnnualIncomeValid : true, showAnnualIncomeMinError: false }); 
                        } else {
                            this.setState({isAnnualIncomeValid : true, showAnnualIncomeMinError: true, PlainAnnualIncome:annualIncome });
                        }
                        }
                    }
            }  else {  this.setState({isAnnualIncomeValid : true, showAnnualIncomeMinError: true}); }
        }
    }
    
    handleProvideSSNChange(isFocus, event) {
        if(!this.state.initialDisabled) {
            let provideSSN = GetSSNMask(event.target.value);
            this.setState({ ProvideSSNValue: provideSSN, isProvideSSNValid : true,  isError: false });            
            if(!isFocus) {
                document.getElementById('frmPrequalify').ssn.type = "password";  
                if(!provideSSN) {  this.setState({isProvideSSNValid : false}); } else {  
                    this.setState({isProvideSSNValid : true});
                    if( provideSSN.length !== 11) {this.setState({ showProvideSSNError: false }); } 
                    else { 
                        if(!IsSSNGroupValid(this.state.ProvideSSNValue)) 
                        { 
                            this.setState({ showProvideSSNError: false }); 
                        } else {
                            this.setState({ showProvideSSNError: true });
                        } 
                    }
                }
            }
            else {document.getElementById('frmPrequalify').ssn.type = "tel";}
        }
    } 
     
      
