import React, { Component } from "react";
import { accessOffersFormFields } from "./Validation/FormData";
import MTextField from "./MTextField";
import { Validator } from "./Validators";
import { emailRegex, isValidInteger , IsSSNGroupValid} from "./Validation/RegexList";
class AccessOffers extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isError: false,
            EmailValue : this.props.email ? this.props.email : '',
            isEmailValid: true,
            showEmailError: true,
            emailFocus: false,
            ProvideSSNValue : '',
            isProvideSSNValid: true,
            showProvideSSNError: true,
            showWarningMessage: this.props.showWarning,
            IsFormSubmitted: false,
            ssnPrefix: 'XXX-XX-',
            OfferExpired: false,
            Offers: [],
            HeadwayLink : `/borrower-info?dealerid=${this.props.dealerid}`
        }
        this.getOfferObject();
    }

    componentDidMount() {
        if(this.props.attemptOver >= 3 ) {
            var timer = setTimeout(function() {
                if(this.props.IsHeadWay) {
                    window.location=`/borrower-info?dealerid=${this.props.dealerid}`;
                }else {
                    window.location='/personal-loans/pre-qualify';
                }
            }, 15000);
        }
        if(!this.state.OfferExpired) {
            if(this.state.EmailValue.length > 0) {
                if(document.getElementById("frmAccessOffer").cssn !== undefined) { document.getElementById("frmAccessOffer").cssn.autofocus = true; }
            }else {
                if(document.getElementById("frmAccessOffer").email !== undefined) { document.getElementById("frmAccessOffer").email.autofocus = true; }
            }
        }        
    }
    getOfferObject() {
        const offerResponse =window.lenderResponse ? window.lenderResponse.Response ? window.lenderResponse.Response : null : null;
        this.state.Offers= offerResponse;
        this.state.OfferExpired =offerResponse !== null ? offerResponse.outcome === 'application expired' ? true : false : false;
    }
    handleEmailChange(isFocus, event) {
        let email = event.target.value;
        this.setState({ EmailValue: email, isEmailValid : true,  showEmailError: true, emailFocus: isFocus, isError: false });
        if(!isFocus) {  
            if(!email) {  this.setState({isEmailValid : false, showEmailError: true }); } else {  
                if(emailRegex.test(email)) {
                    this.setState({isEmailValid : true,  showEmailError: true}); 
                } else { this.setState({isEmailValid : true, showEmailError: false });  
                 }
            }           
        }
    }

    handleProvideSSNChange(isFocus, event) {
            let provideSSN = event.target.value.replace(/X/g,'').replace(/-/g,'');
            //let  = plainSSN;//`${this.state.ssnPrefix}${event.target.value}`;// GetSSNMask(event.target.value);
            if(isValidInteger(provideSSN) && provideSSN.length <= 4) {
            this.setState({ ProvideSSNValue:`${provideSSN}` , isProvideSSNValid : true,  isError: false });            
            if(!isFocus) {
                //document.getElementById('frmAccessOffer').ssn.type = "password";  
                if(!provideSSN) {  this.setState({isProvideSSNValid : false}); } else {  
                    this.setState({isProvideSSNValid : true});
                    if( provideSSN.length !== 4) {this.setState({ showProvideSSNError: false }); } 
                    else { 
                        if(this.state.ProvideSSNValue !== '0000') {
                            this.setState({ showProvideSSNError: true }); 
                        } else {
                            this.setState({ showProvideSSNError: false });
                        }
                    }
                }
            }
            else {document.getElementById('frmAccessOffer').cssn.type = "tel";}
        }
       
    } 
    ValidateInfo() {
        let isValid = true;
        if(!this.state.EmailValue) {  this.setState({isEmailValid : false}); isValid = false; } else if(emailRegex.test(this.state.EmailValue)) {
            this.setState({isEmailValid : true,  showEmailError: true}); 
        } else { this.setState({isEmailValid : true, showEmailError: false }); isValid = false; }
        if(!this.state.ProvideSSNValue) {  this.setState({isProvideSSNValid : false}); isValid = false; } else {  
            this.setState({isProvideSSNValid : true});
            if(this.state.ProvideSSNValue.length !== 4) {this.setState({showProvideSSNError : false }); isValid = false; } 
            else 
            { 
                if(this.state.ProvideSSNValue !== '0000') {
                    this.setState({ showProvideSSNError: true }); 
                } else {
                    this.setState({ showProvideSSNError: false });
                }
            }
        }
        if(isValid)
        {            
            this.setState({ isError: false });
        }
        else {
            this.setState({ isError: true});
        }
        return isValid;

    }
    SubmitForm() {
        if(!this.state.IsFormSubmitted) {
            document.getElementById('frmAccessOffer').submit();
            this.setState({ IsFormSubmitted: true });
        }
    }
    Submit(e){
        e.preventDefault();
        if(this.ValidateInfo())
        {    var that = this;
            this.setState({ showModal : true});
            
            this.SubmitForm();
        }
        
    }
    BeginNewRequest(e) {
        if(this.props.IsHeadWay) {
            window.location=`/borrower-info?dealerid=${this.props.dealerid}`;
        }else {
            window.location='/personal-loans/pre-qualify';
        }
    }
    render() {
        var date =this.state.Offers !== null ? new Date(this.state.Offers.ApplicationInfo.ApplicationDate) : null ,locale = "en-us",  month =date !== null ? date.toLocaleString(locale, { month: "long" }) : null;
        const currentDate = date !== null ? `${month} ${date.getDate()}, ${date.getFullYear()}` : '';
        return (
            <div className="container">
            
                <div className="row">
                    <div className="col-md-12">
                        <div className="box-panel clearfix access-offer-top-margin" id="mainbox" >
                            <div className="container-box-content" id="mainboxcontent">
                               <div className="col-sm-12 custom-pdding">
                                    {this.state.OfferExpired ? <h3>Offer Expired!</h3> :
                                    <h3>Already applied? Access your offers</h3>  }

                                    {!this.state.OfferExpired && this.props.attemptOver < 3 && this.props.currentDevice === "Web" &&
                                        <p className="">If you have already been pre-qualified for a personal loan from our partners, enter your information below and let us find your active offers.<br/> If you have not yet applied, please <a id="mainboxa" href={`${this.props.IsHeadWay ? this.state.HeadwayLink : '/personal-loans/pre-qualify'}`} rel="noopener noreferrer">go to our application form</a> and answer some simple questions to see if you qualify.</p>
                                    }
                                    {!this.state.OfferExpired  && this.props.attemptOver < 3 && this.props.currentDevice === "Mobile" &&
                                        <p className="">Enter your information below and let us find your active offers.<br/> If you have not yet applied, please <a id="mainboxa" href={`${this.props.IsHeadWay ? this.state.HeadwayLink : '/personal-loans/pre-qualify'}`} rel="noopener noreferrer">go to our application form</a> and answer some simple questions to see if you qualify.</p>
                                    }
                                    {this.state.OfferExpired &&
                                    <p className="">Request Summary: ${Validator.amountFormatting(this.state.Offers !== null ? this.state.Offers.ApplicationInfo.loanamount : 0)} for {this.state.Offers !== null ? this.state.Offers.ApplicationInfo.loanpurpose : ''} on {currentDate}</p>
                                    }
                                
                                <div className="loan-app-details">
                                {!this.state.OfferExpired && this.props.attemptOver >= 3 ?   
                                <div>      
                                    <p className="">If you have already been pre-qualified for a personal loan from our partners, enter your information below and let us find your active offers.</p>                       
                                    <div className="row message-box">
                                        <div className="col-md-12 col-xs-12">
                                            <i className="fa fa-exclamation-triangle fa-w-18 fa-3x message-icon" aria-hidden="true">  </i>
                                            <p className="message-text">Unfortunately, we couldnt locate your offers. Please wait while we redirect to our application form where you can complete the pre-qualification process</p>
                                        </div>
                                    </div>
                                    <div className="modal-content-access-offer">                                                                                                
                                        <div className="trans-wrap-access-offer loans-access-offer">
                                        {this.props.IsHeadWay && 
                                            <div className={`${this.props.currentDevice === 'Mobile' ? 'load-img-mobile-hs-access-offer' : 'load-img-hs-accessoffer-hw'}`}>
                                                <i className="fa fa-circle-o-notch fa-spin fa-spin-access-offer"></i>                                                                     
                                                { this.props.currentBrowser === 'safari' ? <img src={`${this.props.configSettings.headWayLogo}`} alt={this.props.configSettings.HeadWayTheme} width="200" /> :
                                                <img src={`${this.props.configSettings.headWayLogo}` } alt={this.props.configSettings.HeadWayTheme } width="200" /> }
                                            </div>
                                        }
                                        {!this.props.IsHeadWay && 

                                            <div className={`${this.props.currentDevice === 'Mobile' ? 'load-img-mobile-hs-access-offer' : 'load-img-hs-accessoffer'}`}>
                                                <i className="fa fa-circle-o-notch fa-spin fa-spin-access-offer"></i>                                                                     
                                                { this.props.currentBrowser === 'safari' ? <img src={`https://${window.cdn}${this.props.configSettings.safariloadImage}`} alt="PrimeRates" width="200" /> :
                                                <img src={ `${this.props.configSettings.loadImage}`} alt={this.props.configSettings.PrimeRateTheme } width="200" /> }
                                            </div>
                                        }
                                                
                                        <div className="website-text">
                                            <p> If you are not redirected within 15 seconds <a id="mainboxa" href="/personal-loans/pre-qualify" rel="noopener noreferrer">click here</a></p>
                                        </div>
                                        </div> 
                                    </div>
                                  </div>
                                
                                :<div>
                                <form action={`${this.props.IsHeadWay ? '/offers' : '/personal-loans/pre-qualify/offers'} `} name="createacc2" id="frmAccessOffer" disable-btn="" className="app-form" method="POST" onSubmit={this.Submit.bind(this)}>
                                    {!this.state.OfferExpired && this.props.attemptOver < 3 && this.props.attemptOver > 0  &&
                                   
                                    <div className="row message-box">
                                        <div className="col-md-12 col-xs-12">
                                            <i className="fa fa-exclamation-triangle fa-w-18 fa-3x message-icon-init" aria-hidden="true">  </i>
                                            <p className="message-text">Unfortunately, we're unable to locate any offers with the information provided. Please check your information and try again.</p>
                                        </div>
                                    </div>
                                    }
                                    {!this.state.OfferExpired && this.props.attemptOver < 3 &&
                                     <div>
                                    <input type="hidden" name="TransactionType" value="reusable_offers_check" />
                                    <div className="row">
                                        <div className="col-md-12 col-xs-12 custom-pdding">
                                        <div className="form-group">
                                        {/* <div className="col-sm-2 custom-pdding">
                                            <label className="control-label custom-pdding" > Email Address</label>
                                        </div> */}
                                            <div className="col-sm-5 custom-pdding">
                                            <MTextField formFields={accessOffersFormFields.Emailaddress} 
                                                    handleChangeEvent={this.handleEmailChange.bind(this)} 
                                                    value={this.state.EmailValue}
                                                    isFocused={this.state.emailFocus}
                                                    showError={this.state.isEmailValid} 
                                                    showErrorMessage= {this.state.showEmailError}
                                                    showCodeErrorMessage={true} showTitle={true}
                                                    name="email" disabled={this.state.initialEnabled}
                                                    tabIndex="1" inputType="email" customeMessage=''
                                                    columnStyle=""/> 
                                            </div>
                                        </div>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md-12 col-xs-12 custom-pdding">
                                        <div className="form-group">
                                        {/* <div className="col-sm-2 custom-pdding">
                                            <label className="control-label custom-pdding" >Social Security Number</label>
                                            <p> (Last 4 Digits)</p>
                                        </div> */}
                                        <input type="hidden" name="dealerid" value={this.props.dealerid} />
                                        <input type="hidden" name="ssn" value={this.state.ProvideSSNValue} />
                                            <div className="col-sm-5 custom-pdding">
                                            
                                                <MTextField formFields={accessOffersFormFields.ProvideSSN} 
                                                handleChangeEvent={this.handleProvideSSNChange.bind(this)} 
                                                value={`${this.state.ssnPrefix}${this.state.ProvideSSNValue}`} 
                                                showError={this.state.isProvideSSNValid} 
                                                showErrorMessage= {this.state.showProvideSSNError}
                                                showCodeErrorMessage={true} IsNumeric={true}
                                                disabled={this.state.initialDisabled}
                                                tabIndex="2" min="11" max="11" name="cssn"
                                                customeMessage=''
                                                inputType="tel" inputMode="numeric"
                                                columnStyle=""/>
                                            </div>
                                        </div>
                                        </div>
                                    </div>
                                    <div className="row">       
                                            <div className={`col-sm-9 custom-pdding ${this.props.currentDevice === "Mobile" ? 'center-text' : ''} `}>                         
                                                <button type="submit" id={`access-my-offers`} name={`access-my-offers`} tabIndex="3" className="btn btn-primary" disabled={this.state.initialDisabled} >Access My Offers</button>
                                            </div>
                                    </div> </div>
                                    }
                                    </form> 
                                    
                                    {this.state.OfferExpired && 
                                        <div>
                                        <div className="row message-box">
                                            <div className="col-md-12 col-xs-12">
                                                <i className="fa fa-exclamation-triangle fa-w-18 fa-3x message-icon" aria-hidden="true">  </i>
                                                <p className="message-text">Unfortunately, your offers have expired. Offers are only valid for 21 days. In order to refresh your offers, please return to the application form and re-enter your information.</p>
                                            </div>
                                        </div>
                                        <br/>
                                        <div className="row">  
                                            <div className={`col-sm-9 custom-pdding ${this.props.currentDevice === "Mobile" ? 'center-text' : ''} `}>                         
                                                <button type="submit" id={`begin-new-request`} name={`begin-new-request`}  tabIndex="3" className="btn btn-primary" disabled={this.state.initialDisabled} onClick={this.BeginNewRequest.bind(this)}>Begin a New Request</button>
                                            </div>
                                            </div>
                                        </div>
                                    }
                                    </div>
                                }
                                
                                </div>
                                </div> 
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )      
          
    }
}

export default AccessOffers;


