    groupOffersBasedOnLoanTerm(offers) {
        let offerGroups = {};
        offers.forEach(offer => {
            if (offer.Term in offerGroups) {
                offerGroups[offer.Term].push(offer);
            } else {
                offerGroups[offer.Term] = [];
                offerGroups[offer.Term].push(offer);
            }
        });

        return offerGroups;
    }
