import React, { Component } from "react";
// import imgCongr from './images/icons/tick1.png';
import { Validator } from "./Validators";
import {loanPurposeList1, defaultValues } from "./AppData";

class LoanIntroduction extends Component {
    // numberWithCommas(x) {
    //     return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    // }
    constructor(props) {
        super(props);
        const filteredLoanPurpose = loanPurposeList1.filter(lp => {
            if(this.props.loanPurpose) {
                return (lp.plKey.toLowerCase() === this.props.loanPurpose.toLowerCase() || lp.key.toLowerCase() === this.props.loanPurpose.toLowerCase() )
                                && this.props.loanPurpose.length > 0  ;
            }
            return filteredLoanPurpose;
        });
        this.state = {
            selectedLoanPurpose: filteredLoanPurpose.length > 0 ? filteredLoanPurpose[0].key :  defaultValues.defaultSelect,
        }
    }
    render() {
        // const preQualifiedMessage = this.props.selectedOffers.length > 0 ?
        //     ` for $${Validator.amountFormatting(this.props.loanAmount)}` : ' for the offers shown below';
        //const lenderName = this.props.lenders.toLowerCase() === 'marlette' ? 'Best Egg' : this.props.lenders ;
        const lenderDisplay = this.props.lendercount.length > 1 ? 'lenders' : 'lender';
        var date =this.props.IsReusableOffers ? new Date(this.props.ApplicationInfo.ApplicationDate) : new Date(),locale = "en-us",  month = date.toLocaleString(locale, { month: "long" });
        const currentDate = `${month} ${date.getDate()}, ${date.getFullYear()}`;
        return (
            <div className="row">
                
                <div className="col-md-12">
                {this.props.currentDevice === 'Web' ? 
                <h3 className="congr">{this.props.IsReusableOffers ? "Welcome back" : ""} <span className="capitalize">{ this.props.IsReusableOffers ? `${this.props.ApplicationInfo.firstname}!`  : ''}</span> 
                    {this.props.offers  &&
                    ` You have ${this.props.offers.length} pre-qualified offers from  ${this.props.lendercount.length} ${lenderDisplay} in our network.`
                    }
                    </h3>
                    :
                    <h3 className="">{this.props.IsReusableOffers ? "Welcome back" : ""} <span className="capitalize">{ this.props.IsReusableOffers ? `${this.props.ApplicationInfo.firstname}!` :  ''}</span> 
                    {this.props.offers  &&
                    ` You have ${this.props.offers.length} pre-qualified offers from  ${this.props.lendercount.length} ${lenderDisplay} in our network.`
                    }
                    </h3>
                }
                {/* {this.props.currentDevice === 'Web' &&  
                    <p>If you're interested in pursuing one of them, click "Claim Offer" below. You'll complete the final steps on the partner's site, but don't worry, we'll send them the information you've already provided. You won't have to re-enter information.</p>
                } */}
                     {this.props.offers  &&
                    <p className="requested-text">${Validator.amountFormatting(this.props.loanAmount)} requested for {this.state.selectedLoanPurpose} on {currentDate}</p>
                    }
                </div>
            </div>
        );
    }
}

export default LoanIntroduction;


