import axios from "axios";
 /*
//Local only
const configJsonPath = "/static/json/PL_LA_Configuration.json";
 */

// Production only
 const configJsonPath = "/wp-content/plugins/prime-rates-loan-applications/static/json/PL_LA_Configuration.json";

// demo server
//DEMO_DEPLOY const configJsonPath = "/pr_loans/wp-content/plugins/prime-rates-loan-applications/static/json/PL_LA_Configuration.json";
// Replace in CC_MP_Configuration.json => 
// "srcJsonPath": "/wp-content/plugins/prime-rates/static/json/CC_Marketplace_Table.json",
let Configuration = null;


function getConfigurationJson() {
    const promise = axios.get(configJsonPath);
    return promise.then(result => {        
        return { Configuration: result.data };
    });
}

export { Configuration, getConfigurationJson }


