        LogInvalidData() {
            setTimeout(() => {
                let fields = `Field Name:`;
                if(!this.state.FirstNameValue) { fields = `${fields}FirstName, Values:'${this.props.PostData.firstname}',`;}
                if(!this.state.LastNameValue) { fields = `${fields}LastName, Values:'${this.props.PostData.lastname}',`;}
                if(!this.state.EmailValue || !this.state.isEmailValid || !this.state.showEmailError) { fields = `${fields}Email, Values:'${this.props.PostData.email}',`;}
                if(!this.state.StreedAddressValue) { fields = `${fields}StreetAddress, Values:'${this.props.PostData.streetaddress}',`;}
                if(!this.state.CityValue) { fields = `${fields}City, Values:'${this.props.PostData.city}',`;}
                if(!this.state.ZipcodeValue || !this.state.showZipcodeError) { fields = `${fields}ZipcodeValue, Values:'${this.props.PostData.zipcode}',`;}
                if(!this.state.BirthdateValue || this.state.BirthdateValue.length < 10 || !this.state.showBirthdateError) { fields = `${fields}Birthdate, Values:'${this.props.PostData.birthdate}',`;}
                if(!this.state.AnnualIncomeValue || !this.state.showAnnualIncomeMinError) { fields = `${fields}AnnualIncome, Values:'${this.props.PostData.annualincome}',`;}
                if(!this.state.PhoneNumberValue || !this.state.showPhoneNumberError || !this.state.showPhoneNumberCodeError) { fields = `${fields}Phonenumber, Values:'${this.props.PostData.phonenumber}',`;}
                if(!this.state.ProvideSSNValue || !this.state.showProvideSSNError) { fields = `${fields}SSN, Values:'${this.props.PostData.ssn}',`;}
                if(!this.state.LoanAmountValue || !this.state.showLoanAmountMinError ) { fields = `${fields}LoanAmount, Values:'${this.props.PostData.loanamount}',`;}
                if(this.state.selectedState === 'Select') {  fields = `${fields}State, Values:'${this.props.PostData.state}',`;}
                if(this.state.selectedHousing === 'Select') {fields = `${fields}Housing, Values:'${this.props.PostData.housing}',`;}
                if(this.state.selectedHousing === 'RENT' || this.state.selectedHousing === 'OWN_WITH_MORTGAGE' || this.state.selectedHousing === 'Own  with mortgage')
                {
                    if(!this.state.HousingPaymentValue) {  fields = `${fields}HousingPayment, Values:'${this.props.PostData.housingPayment}',`;}
                }
                if(this.state.selectedEmployment === 'Select') { fields = `${fields}Employment, Values:'${this.props.PostData.employment}',`;}
                if(!this.state.IsIntercom && this.state.selectedEstimatedCreditScore === 'Select') { fields = `${fields}CreditScore, Values:'${this.props.PostData.creditscore}',`;}
                if(this.state.selectedTimeAtCurrentAddress === 'Select') { fields = `${fields}TimeAtCurrentAddress, Values:'${this.props.PostData.currenttime}',`;}

                let source = `action=log_message&nonce=4109b2257c&logMessage=Validation error occurred on ${this.props.affiliateid} call with fields: ${fields}`;
                SendEmail(this.props.configSettings.emailSendEndpoint, source);
            }, 2500); 
        }
        initAutocomplete() {
            // Create the autocomplete object, restricting the search to geographical
            // location types.
            try {
                autocomplete = new window.google.maps.places.Autocomplete(
                    /** @type {!HTMLInputElement} */(document.getElementById('streetaddress')),
                    {types: ['geocode']});
        
                // When the user selects an address from the dropdown, populate the address
                // fields in the form.
                autocomplete.addListener('place_changed', this.fillInAddress.bind(this));
                autocomplete.setComponentRestrictions({'country': ['us']});
            }catch(e) {
                
            }       
        }

        fillInAddress() {
            // Get the place details from the autocomplete object.
            var place = autocomplete.getPlace();        
            this.setState({ StreedAddressValue: '' , ApptValue : '', CityValue: '', 
            selectedState: defaultValues.defaultSelect, ZipcodeValue: '', isStateValid: true, isCityValid: true, isZipcodeValid: true, showCityError: true, showZipcodeError: true});
            // Get each component of the address from the place details
            // and fill the corresponding field on the form.
            let streetAdd = ''; let aprtmentAdd = ''; let city = ''; let state = ''; let zip = '';

            for (var i = 0; i < place.address_components.length; i++) {
            var addressType = place.address_components[i].types[0];
            if (componentForm[addressType]) {
                var val = place.address_components[i][componentForm[addressType]];
                if(addressType === 'street_number') {
                    streetAdd = val;
                }
                if(addressType === 'route') {
                    aprtmentAdd = val;
                }
                if(addressType === 'locality') {
                    city = val;
                }
                if(addressType === 'administrative_area_level_1') {
                    state = val;
                }
                if(addressType === 'postal_code') {
                    zip = val;
                }
            }
            }         
            let findState = this.props.IsHeadWay ? stateHWList.find(o => o.key === state) : stateList.find(o => o.key === state);
            if(findState !== null && findState !== undefined) {
                this.setState({ StreedAddressValue: `${streetAdd} ${aprtmentAdd}` , ApptValue : '', CityValue: city, CityChangedValue : city, 
                selectedState: state, selectedStateChanged: state, ZipcodeValue: zip, ZipcodeChangedValue : zip, showInvalidAddressModal: false});
            }
            else {
                this.setState({ showInvalidAddressModal: true});
            }
            
        }

