import React, { Component } from "react";
import { Validator } from "./Validators";
import LenderResultRowDetail from "./LenderResultRowDetail";


class LenderResultRow extends Component {
    constructor(props) {
        super(props);
        this.validations = Validator;
        this.state = { isDetailExpanded: false, totalInterestFees: 0.0 };
        this.handleOfferDetailToggle = this.handleOfferDetailToggle.bind(this);
        this.ShowLenderOffers = this.ShowLenderOffers.bind(this);
    }
    handleOfferDetailToggle(event) {
        // event.preventDefault();
        this.setState((prevState) => ({
            isDetailExpanded: !prevState.isDetailExpanded
        }));
        if(window.dataLayer !== undefined) {
            window.dataLayer.push({'event': 'optimize.activate'});
        }
    }

    componentDidUpdate() {
        // Hiding offer detail on update result clicked if it was opened previously
        if (this.props.isLoading && this.state.isDetailExpanded) {
            this.setState({ isDetailExpanded: false });
        }
    }
    ShowLenderOffers(lender,event) {
        this.props.offersLinkClicked(lender, true);
    }
    getLenderDisplayName(lender) {
        var lenderId = lender;
        if(lender  === 'Marlette') { lenderId = 'Best Egg'; }
        return lenderId;
    }
    getTotalInterestAndFees(apr, monthlyPayment, totalMonths, totalPrinciple) {
        //calculate net principal, calculate APR devide by 100 
        let periodRate = (apr/100) / 12;
        let paymentFactor = ((periodRate * Math.pow(periodRate + 1,totalMonths) ) / (Math.pow((periodRate + 1),totalMonths) - 1 ));
        let netPrinciple = monthlyPayment / paymentFactor;
        //calculate origination fee as total principal less net principal
        let originationFees = totalPrinciple - netPrinciple;
        //calculate interest cost as total of monthly payments less original principal value
        let interestCost = (monthlyPayment * totalMonths) - totalPrinciple;
        //calculate total cost
        let totalCost = originationFees + interestCost;
        return totalCost.toFixed();
    }    
    Submit(e){
        if(!(window.isHeadway === 1 || window.isHeadway === "1")) {
            window.shopper_func();
        }
        window.offerclick();
    }
    render() {
        const toggleOfferDetailLabel = this.state.isDetailExpanded ? 'Hide Partner Details' : 'Partner Details';
        const lender = { lenderId: this.props.offer.Lender };        
        const offerItem = this.props.offer;
        const uuid = this.props.UUID;
        const selected = this.props.offer.isSelectedLender ? ' selectedLender ' : '';       
        const rowClass = (this.props.lenderIndex % 2) !== 0 ? ` ${selected}` : `${selected}`;
        const monthlyPayment = 'MonthlyPayment' in offerItem ? offerItem.MonthlyPayment : offerItem.MonthlyPayment;
        const monthlyPaymentFloat = (parseFloat(monthlyPayment, 10)).toFixed();
        const apr = (Math.round(offerItem.APR * 100) / 100).toFixed(2);
        const totalCost = this.getTotalInterestAndFees(offerItem.APR,monthlyPayment,offerItem.Term,offerItem.LoanAmount); //(Math.round(monthlyPayment * this.props.term * 100) / 100).toFixed();
        const offerId = offerItem.OfferID ? offerItem.OfferID : '';
        const showHideLink = this.props.lenderIndex;//lender.lenderId + '-' + offerItem.LoanAmount + '-' + offerItem.Term;        
        const claimOfferText ="Select";
        return (
            [  this.props.currentDevice  !== 'Web' &&  this.props.currentDevice  !== 'Tab' ?             
            <ul className={`row ${rowClass} ${this.props.offer.isSelectedLender ? ' active ' : ''}`} id={this.props.lenderIndex} key={'ul1-' + this.props.lenderIndex}>
            <li className="partner-logo col-sm-2 " key={'li1-' + this.props.lenderIndex}>
                <a key={'li-a-' + this.props.lenderIndex}>
                    <img key={'img-' + this.props.lenderIndex} id={`id-image-${offerItem.Lender}`} name={`name-image-${offerItem.Lender}`} alt={offerItem.Lender} src={offerItem.lenderImage} className={``}/>
                </a>
                {/* <span key={'li-span-' + this.props.lenderIndex} className="annotation">Partner</span> */}
                {this.props.disclaimer ? 
                <span className="show-details" key={'li-span-b' + lender.lenderId + '-' + offerItem.LoanAmount + '-' + offerItem.Term
            }>
                        <a key={'li-span-a-' + lender.lenderId} href={`#${showHideLink}`} onClick={this.handleOfferDetailToggle}>{toggleOfferDetailLabel}</a>
                    </span> : '' }
                   
            </li>
            {this.props.lenderOffersCount > 0 ?
            <li className="offer-count col-sm-2 " key={'li231-' + this.props.lenderIndex} >
                    <span className="show-details bigtxt">
                    {this.props.lenderOffersCount > 0 ? this.props.lenderOffersCount === 1 ?  <a onClick={this.ShowLenderOffers.bind(this, offerItem.Lender)}><u><b> {this.props.lenderOffersCount} Offer</b></u></a> :
                        <a onClick={this.ShowLenderOffers.bind(this, offerItem.Lender)}><u><b> {this.props.lenderOffersCount} Offers</b></u></a> : ''}
                    </span>
                </li>
                : '' }
            <li className="amount col-sm-2 col-xs-6 topmrgn"> <span className="amount-title" key={'mp-anno-' + this.props.keyId}><b>Amount</b></span> 
             <span className="amount-value" key={'mp-val1-' + this.props.keyId} >${Validator.amountFormatting(offerItem.LoanAmount)}</span>
                </li>
            <li className="apr col-sm-2 col-xs-6 topmrgn" key={'li-cell-' + this.props.lenderIndex}>
                <span key={'annotation-txt' + this.props.lenderIndex} className="apr-title"><b>APR</b></span>
                <span key={'span-apr-per' + this.props.lenderIndex} className="apr-value">{apr}%
                {offerItem.Lender.toLowerCase() === 'lightstream' ?  <a href="" key={'li-monthly-a-' + lender.lenderId} className="info-tooltip">?<span key={'li-monthly-span-' + lender.lenderId} className="tooltiptext">With AutoPay discount</span>
                        </a> : ''}</span>                
            </li>
            <li className="term col-sm-2 col-xs-6 topmrgn" key={'total-val-' + this.props.lenderIndex}>
                <span className="term-title" key={'total-anno-' + this.props.lenderIndex}><b>Term</b></span>
                <span className="term-value" key={'total-big-' + this.props.lenderIndex}>{offerItem.Term} Years</span>
            </li>
            <li className="monthlypayment col-sm-2 col-xs-6 topmrgn" key={'mp-' + this.props.lenderIndex}>
                <span className="monthlypayment-title" key={'mp-anno2-' + this.props.lenderIndex}><b>Monthly Payment</b></span>
                <span className="monthlypayment-value" key={'mp-val2-' + this.props.lenderIndex} >${Validator.amountFormatting(monthlyPaymentFloat)}</span>
            </li>
            
            <li className="selectLoanButton col2 col-sm-2" key={'claim-' + this.props.lenderIndex}>
            {offerItem.Lender === 'OneMain' && offerItem.Secure === 'S' ? 
            <a className={`loan-app info-tooltip tooltip-width ${window.isHeadway === 1 || window.isHeadway === "1" ? ' secure-banner-hs ' : ' secure-banner '}`}>            
            <span className="tooltiptext tooltip-top-margin-securelink">This offer requires collateral to secure a loan. Examples of acceptable collateral include automobiles, trucks, motorcycles, boats, campers, and RVs. All collateral will be reviewed to determine whether it is of sufficient value. Vehicles used as collateral must also be insured against physical damage for the term of the loan. Active duty military, their spouse and dependents covered by the Military Lending Act may not pledge any vehicle as collateral. Contact OneMain for more information at 1-800-961-5577.</span>
                Secured Offer</a> : '' }
                <form action={lender.lenderId === 'Freedom' ? '/freedom-plus-thank-you' : this.props.redirectUrl} onSubmit={this.Submit.bind(this)}
                    target="_blank" method="post">
                    { !window.disableClaimOffer &&
                    <div>
                    <input type="hidden" name="logo"
                        value={encodeURIComponent(offerItem.lenderImage)} />
                    <input type="hidden" name="url"
                        value={ encodeURIComponent(offerItem.OfferURL)} />
                    <input type="hidden" name="lender_name" value={encodeURIComponent(offerItem.Lender)} />
                    <input type="hidden" name="logo_type" value="LA" />
                    <input type="hidden" name="uuid" value={uuid} />
                    <input type="hidden" name="ApplicationID" value={offerItem.ApplicationId} />
                    <input type="hidden" name="offerId" value={offerId} />
                    <input type="hidden" name="isHeadway" value={window.isHeadway} />
                    <input type="hidden" name="hsessionid" value={window.hSessionID ? window.hSessionID : ''} />
                    <input type="hidden" name="ReusableOffer" value={this.props.IsReusableOffers ? 'Y' : 'N'} />
                    <input type="hidden" name="email" value={window.userEmail} />
                    <input type="hidden" name="loanamount" value={offerItem.LoanAmount}/>
                    <input type="hidden" name="pr_clid" value={window.prclid ? window.prclid : ''} />
                    <input type="hidden" name="pr_sessionid" value={window.prsessionid ? window.prsessionid : ''} />
                    </div>
                    }
                    <button type="submit" className="btn btn-primary ipad-btn-primary button-width-full" id={'select-' + offerItem.Lender} name={'select-' + offerItem.Lender} key={'btn-' + this.props.keyId} disabled={window.disableClaimOffer}>{claimOfferText}&nbsp;&nbsp;<span className="fa fa-lock" aria-hidden="true"></span></button>
                </form>
                <span className="annotation" key={'btn-text-' + this.props.lenderIndex}>on {this.getLenderDisplayName(lender.lenderId)}'s secure website</span>
            </li>
        </ul> : <div className="sorttable_block">
            <ul className={`row  ${this.props.offer.isSelectedLender ? ' selectedLender-top active ' : ''}`} id={this.props.lenderIndex} key={'ul2-' + this.props.lenderIndex}>
                <li className="partner-logo col-sm-3 " key={'li2-' + this.props.lenderIndex}>
                    <a key={'li-a-' + this.props.lenderIndex}>
                        <img key={'img2-' + this.props.lenderIndex} id={`id-image-${offerItem.Lender}`} name={`name-image-${offerItem.Lender}`} alt={offerItem.Lender} src={offerItem.lenderImage} className={`${lender.lenderId === 'Prosper' || lender.lenderId === 'Freedom' ? ' partner-logo-image ' : ''}`} />
                    </a>
                    {/* <span key={'li-span-' + this.props.lenderIndex} className="annotation">Partner</span> */}
                    {this.props.disclaimer ? 
                    <span className="show-details" key={'li-span-b' + lender.lenderId + '-' + offerItem.LoanAmount + '-' + offerItem.Term
                }>
                            <a key={'li-span-a-' + lender.lenderId} href={`#${showHideLink}`} onClick={this.handleOfferDetailToggle}>{toggleOfferDetailLabel}</a>
                        </span> : '' }
                       
                    
                </li>
                <li className="offer-count col-sm-3 " key={'li232-' + this.props.lenderIndex}>
                <span className="show-details bigtxt">
                    {this.props.lenderOffersCount > 0 ? this.props.lenderOffersCount === 1 ?  <a onClick={this.ShowLenderOffers.bind(this, offerItem.Lender)}><u><b> {this.props.lenderOffersCount} Offer</b></u></a> :
                        <a onClick={this.ShowLenderOffers.bind(this, offerItem.Lender)}><u><b> {this.props.lenderOffersCount} Offers</b></u></a> : ''}
                    </span>
                </li>
                <li className="loan-cell col-sm-3 " key={'li233-' + this.props.lenderIndex}>
                    {/* <span className="show-details bigtxt">
                    {this.props.lenderOffersCount > 0 ? 
                        <a onClick={this.ShowLenderOffers.bind(this, offerItem.Lender)}><u><b> {this.props.lenderOffersCount} Offers</b></u></a> : ''}
                    </span> */}
                </li>
                <li className="selectLoanButton mobile-cell col2 col-sm-3" key={'claim-' + this.props.lenderIndex}>
                {offerItem.Lender === 'OneMain' && offerItem.secure === 'S' ? 
                <a className={`loan-app info-tooltip tooltip-width ${window.isHeadway === 1 || window.isHeadway === "1" ? ' secure-banner-hs ' : ' secure-banner '}`}>            
                <span className="tooltiptext tooltip-top-margin-securelink">This offer requires collateral to secure a loan. Examples of acceptable collateral include automobiles, trucks, motorcycles, boats, campers, and RVs. All collateral will be reviewed to determine whether it is of sufficient value. Vehicles used as collateral must also be insured against physical damage for the term of the loan. Active duty military, their spouse and dependents covered by the Military Lending Act may not pledge any vehicle as collateral. Contact OneMain for more information at 1-800-961-5577.</span>
                    Secured Offer</a> : '' }
                    <form action={lender.lenderId === 'Freedom' ? '/freedom-plus-thank-you' : this.props.redirectUrl} onSubmit={this.Submit.bind(this)}
                        target="_blank" method="post">
                         { !window.disableClaimOffer &&
                    <div>
                        <input type="hidden" name="logo"
                            value={encodeURIComponent(offerItem.lenderImage)} />
                        <input type="hidden" name="url"
                            value={encodeURIComponent(offerItem.OfferURL)} />
                        <input type="hidden" name="lender_name" value={encodeURIComponent(offerItem.Lender)} />
                        <input type="hidden" name="logo_type" value="LA" />
                        <input type="hidden" name="uuid" value={uuid} />
                        <input type="hidden" name="ApplicationID" value={offerItem.ApplicationId} />
                        <input type="hidden" name="offerId" value={offerId} />
                        <input type="hidden" name="isHeadway" value={window.isHeadway} />
                        <input type="hidden" name="hsessionid" value={window.hSessionID ? window.hSessionID : ''} />
                        <input type="hidden" name="ReusableOffer" value={this.props.IsReusableOffers ? 'Y' : 'N'} />
                        <input type="hidden" name="email" value={window.userEmail} />
                        <input type="hidden" name="loanamount" value={offerItem.LoanAmount}/>
                        <input type="hidden" name="pr_clid" value={window.prclid ? window.prclid : ''} />
                        <input type="hidden" name="pr_sessionid" value={window.prsessionid ? window.prsessionid : ''} />
                        </div> }
                        <button type="submit" className="btn btn-primary ipad-btn-primary" id={'select-' + offerItem.Lender} name={'select-' + offerItem.Lender} key={'btn1-' + this.props.lenderIndex} disabled={window.disableClaimOffer}>{claimOfferText}&nbsp;&nbsp;<span className="fa fa-lock" aria-hidden="true"></span></button>
                        
                    </form>
                    <span className="annotation" key={'btn-text1-' + this.props.keyId}>on {this.getLenderDisplayName(lender.lenderId)}'s secure website</span>
                </li>
                </ul>
                <ul className={`row  ${this.props.offer.isSelectedLender ? ' selectedLender-bottom active ' : ''}`} key={'ul-cell-' + this.props.lenderIndex}>
                <li className="amount col-sm-3 col-xs-6 topmrgn" key={'li-cell1-' + this.props.lenderIndex}>
                    <span className="amount-title" key={'mp-anno-' + this.props.lenderIndex}><b>Amount</b></span> 
                    <span className="amount-value" key={'mp-val3-' + this.props.lenderIndex} >${Validator.amountFormatting(offerItem.LoanAmount)}</span>
                </li>
                {/* <li className="loan-cell col-sm-2 col-xs-6 topmrgn">  <span className="bigtxt number" key={'mp-val-' + this.props.lenderIndex} >${Validator.amountFormatting(offerItem.loanAmount)}</span>
                    <span className="annotation" key={'mp-anno-' + this.props.lenderIndex}>Loan Amount</span> </li> */}
                <li className="apr col-sm-3 col-xs-6 topmrgn" key={'li-cell2-' + this.props.lenderIndex}>
                    <span key={'apr-title-' + this.props.keyId} className="apr-title"><b>APR</b> {offerItem.Lender.toLowerCase() === 'lightstream' ?  <a href="" key={'li-monthly-a-' + lender.lenderId} className="info-tooltip">?<span key={'li-monthly-span-' + lender.lenderId} className="tooltiptext">With AutoPay discount</span>
                        </a> : ''}</span>
                    <span key={'span-apr-' + this.props.keyId} className="apr-value">{apr}%</span>
                </li>
                {/* <li className="loan-cell col-sm-3 col-xs-6 topmrgn" key={'mp-' + this.props.lenderIndex}>
                    <span className="bigtxt  number" key={'mp-val-' + this.props.lenderIndex} >${Validator.amountFormatting(monthlyPaymentFloat)}</span>
                    <span className="annotation" key={'mp-anno-' + this.props.lenderIndex}>Monthly Payment for <b>{offerItem.loanTermInMonths / 12}</b> yrs</span>
                </li> */}
                <li className="term mobile-cell col-sm-3 col-xs-6 topmrgn" key={'total-val1-' + this.props.lenderIndex}>
                    <span className="term-title" key={'total-anno-' + this.props.lenderIndex}><b>Term</b></span>
                    <span className="term-value" key={'total-big-' + this.props.lenderIndex}>{offerItem.Term} Years</span>
                </li>
                <li className="monthlypayment mobile-cell col-sm-3 col-xs-6 topmrgn" key={'total-val2-' + this.props.lenderIndex}> 
                    <div className="align-center">
                        <span className="monthlypayment-title" key={'mp-anno-mon' + this.props.lenderIndex}> <b>Monthly Payment</b></span> 
                        <span className="monthlypayment-value" key={'mp-val-mon' + this.props.lenderIndex} >${Validator.amountFormatting(monthlyPaymentFloat)}</span>
                    </div>
                </li>
            </ul> </div>,
             <LenderResultRowDetail key={'detail-' + this.props.lenderIndex}
             isDetailExpanded={this.state.isDetailExpanded}
             lender={lender} hideLink={showHideLink}
             disclaimer={this.props.disclaimer}
             rowClass={rowClass}
             onHideDetailClick={this.handleOfferDetailToggle}
             toggleOfferDetailLabel={toggleOfferDetailLabel}
             lenderId={this.props.lenderIndex} /> ]
        );
    }
}

export default LenderResultRow;



